import React from 'react'

function Header(props){
    let value= props.value.join('')
    if(props.result===true){
        if(value.indexOf('÷')){
            value= value.replace('÷','/')
        }
        if(value.indexOf('x')){
            value= value.replace('x','*')
        }
    try {
        value= eval(value)
    } catch (error) {
        value= 'Error'
    }
    }

    

    return(
        <div className="header">
        <p>{value?value:"0"}</p>
        </div>
    )
}
export default Header