import React, { Component , Fragment } from 'react';
import Header from './header'
import Cbutton from './button'
class Mainc extends Component {
    state={
        calculation:[],
        result:false
    }
    AcHendeler=()=>{
        this.setState({
            calculation:[]
        })
    }

     checkLastvalue= (cur)=>{
       let checkvalu=['÷','x','-','+']
       const prev= this.state.calculation[this.state.calculation.length-1]
       for (const i of checkvalu) {
        if(prev===i){
            for (const e of checkvalu) {
               if(cur===e){
                   return true
               }
            }
        }
       }
       return false
   }
   checkerro=()=>{
    let checkvalu=['÷','x','-','+']
    const prev= this.state.calculation[this.state.calculation.length-1]
    console.log(prev)
    for (const i of checkvalu) {
         if(prev===i){
             return true;
         }

    }
    return false;
   }

    hendeler=(name)=>{
        
        
        if(this.state.result===true){
            this.setState({
                calculation:[],
                result:false
               })
           }else{
           console.log(this.state)
           if(this.state.calculation.length<12){

            if(!this.checkLastvalue(name)){
                this.setState({
                    calculation:[...this.state.calculation,name]
                })
            }else{

                this.setState({
                    calculation:[...this.state.calculation.slice(0,this.state.calculation.length-1),name]
                }) 
            }
            console.log(this.state)
        }}
  }

  ansex= ()=>{
    
      this.setState({
        result:true
      })
  }
    hendelerAns=()=>{
        if(this.checkerro()){
            this.setState({
                calculation:[...this.state.calculation.slice(0,this.state.calculation.length-1)]
            }) 
            this.ansex()
        }else{
            this.ansex()
        }
       

  }
   
    render() {
        return (
            <Fragment>
                <Header value={this.state.calculation} result={this.state.result}></Header>
                <div className="gird-container">
                <Cbutton onchange={this.hendeler} name="7" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="8" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="9"stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="÷" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="4" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="5" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="6" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="x" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="1" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="2" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="3" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="-" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="0" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.AcHendeler} name="AC" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendeler} name="+" stl="grid-item"></Cbutton>
                <Cbutton onchange={this.hendelerAns} name="=" stl="grid-item btnbg"></Cbutton>
                </div>
            </Fragment>
        );
    }
}

export default Mainc;